import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';
import App from './App';

import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import Cerveza from '@brewkit/themes/cerveza';
import { color, size } from '@brewkit/themes/cerveza/variables';

const themeModified = {
    typography: {
        caption: {
            fontWeight: 'normal',
            fontSize: '0.75rem',
            lineHeight: 1.33,
            color: color.blueGray.dark,
        },
        body1: {
            fontSize: '1rem',
        }
    },
    props: {

        MuiTabs: {
            indicatorColor: "primary",
            textColor: "primary",
        },

        MuiTooltip: {
            arrow: true,
        },
    },
    overrides: {
        MuiTabs: {

            root: {
                borderBottom: '2px solid ' + color.brand.tertiary,
            },

            indicator: {
                height: size.xxsmall,
            },
        },

        MuiTab: {

            root: {
                padding: '1rem 1rem 0.875rem 1rem',
                fontSize: size.small,
                lineHeight: 1,
                fontWeight: 'bold',

                '@media (min-width: 600px)': {
                    minWidth: 0
                },
            },

            textColorPrimary: {
                color: color.blueGray.dark,
            },

            textColorSecondary: {
                color: color.blueGray.dark,
            },

            wrapper: {
                flexDirection: 'row',
            },

            labelIcon: {
                minHeight: 0,
                paddingTop: size.normal,
                '& $wrapper > *:first-child': {
                    marginBottom: 0,
                    marginRight: size.xsmall,
                },
            },

            wrapped: {
                fontSize: size.small,
                lineHeight: 1,
            },

        },

        MuiTooltip: {

            tooltip: {
                padding: size.small,
                fontSize: size.small,
                backgroundColor: color.accent.info,
                color: color.blueGray.lightest,
                minWidth: '150px',
                maxWidth: '240px',
                borderRadius: size.xxsmall,
                boxShadow: "0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12)"
            },

            arrow: {
                color: color.accent.info,
            }
        }
    },

};

const theme = createMuiTheme(Cerveza, themeModified);


ReactDOM.render(
    <ThemeProvider theme={theme} >
        <App />
    </ThemeProvider>,
    document.getElementById('root')
);

