import React, { useEffect } from 'react';
import { BrowserRouter, Route, useHistory } from "react-router-dom";
import './App.css';

import MarkdownRenderer from './MarkdownRenderer'

const driver_path = "/c4z/variable_parser_agent/www/documentation/";

function MarkDown() {
    let [markdown, setMarkdown] = React.useState('');
    const history = useHistory();

    useEffect(() => {
        fetch('../documentation.md')
            .then(response => response.text())
            .then(text => {
                history.push(driver_path)

                setMarkdown(text);
            });
    }, [history])

    return (
        <div className="result-pane">
            <MarkdownRenderer
                className="result"
                source={markdown}
            />
        </div>
    );
}

function App() {
    return (
        <BrowserRouter>
            <Route path={driver_path} component={MarkDown} />
        </BrowserRouter>
    );
}

export default App;
