import React from 'react';
import { Checkbox } from "@brewkit/components";
import {
    withStyles,
    createStyles
} from '@material-ui/core/styles';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import {
    Table,
    TableContainer,
    TableRow,
    TableBody,
    TableCell,
    TableHead,
    Typography,
    Fab,
    Zoom,
    useScrollTrigger
} from '@material-ui/core';

import Markdown from 'react-markdown'

import CodeBlock from './CodeBlock'

function getCoreProps(props) {
    return props['data-sourcepos'] ? { 'data-sourcepos': props['data-sourcepos'] } : {}
}

export default function MarkdownRenderer(props) {
    function Heading(props) {
        function flatten(text, child) {
            return typeof child === 'string'
                ? text + child
                : React.Children.toArray(child.props.children).reduce(flatten, text)
        }

        var children = React.Children.toArray(props.children)
        var text = children.reduce(flatten, '')
        var slug = text.toLowerCase()
            .replace(/\//g, '')
            .replace(/\W/g, '-')
            .replace(/--/g, '-')
            .replace(/((?!\d)-(\d))/g, '$2') //TODO: there has to be a better way

        const style = props.level > 1 ? { marginTop: "5px", marginBottom: "5px" } : { marginBottom: "5px" }

        return (
            <Typography
                id={slug}
                variant={`h${props.level + 1}`}
                color="primary"
                style={style}
            >
                {props.children}
            </Typography>
        )
    }

    function ParagraphRenderer(props) {
        return (
            <Typography style={{ marginBottom: "8pt", marginTop: "8pt" }} variant="body1">{props.children}</Typography>
        )
    }

    function ListRenderer(props) {
        const attrs = getCoreProps(props)
        if (props.start !== null && props.start !== 1 && props.start !== undefined) {
            attrs.start = props.start.toString()
        }

        return React.createElement(props.ordered ? 'ol' : 'ul', attrs, props.children)
    }

    function ListItemRenderer(props) {
        let checkbox = null
        if (props.checked !== null && props.checked !== undefined) {
            const checked = props.checked
            checkbox = <Checkbox isChecked={checked} isDisabled />
        }

        return (
            <li>
                {checkbox}
                <Typography {...getCoreProps(props)} variant="body2">{props.children}</Typography>
            </li>
        )
    }

    function InlineCodeRenderer(props) {
        return (
            <code
                style={{
                    background: "#CEEEEE"
                }}
                {...getCoreProps(props)}
            >
                {props.children}
            </code>
        );
    }

    const StyledTableCell = withStyles((theme) =>
        createStyles({
            head: {
                backgroundColor: "#03a9f4",
                color: "white"
            },
            body: {
                fontSize: 14,
                fontWeight: "bold"
            }
        })
    )(TableCell);

    const StyledTableRow = withStyles((theme) => ({
        root: {
            '&:nth-of-type(odd)': {
                backgroundColor: theme.palette.action.hover,
            },
        },
    }))(TableRow);

    const ScrollToTop = (props) => {
        const trigger = useScrollTrigger({
            disableHysteresis: true,
            threshold: 100,
        });

        const handleClick = (event) => {
            const anchor = (event.target.ownerDocument || document).querySelector('#back-to-top-anchor');

            if (anchor) {
                anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
        };

        return (
            <Zoom in={trigger}>
                <div onClick={handleClick} role="presentation" style={{
                    position: 'fixed',
                    bottom: "20px",
                    right: "20px",
                    zIndex: 9000
                }}>
                    {props.children}
                </div>
            </Zoom>
        );
    }

    const gfm = require('remark-gfm')

    return (
        <div
            style={{
                padding: "10px",
                paddingLeft: "20px"
            }}
        >
            <div id="back-to-top-anchor" />
            <Markdown
                plugins={gfm}
                className="result"
                source={props.source}
                renderers={{
                    break: 'br',
                    paragraph: ParagraphRenderer,
                    emphasis: 'em',
                    strong: 'strong',
                    thematicBreak: 'hr',
                    blockquote: 'blockquote',
                    delete: 'del',
                    link: 'a',
                    image: (props) => <img style={{ maxWidth: "calc(100vw - 40px)" }} alt={props.alt} src={`..${props.src.substring(1)}`} />,
                    linkReference: 'a',
                    imageReference: 'img',

                    heading: Heading,
                    list: ListRenderer,
                    listItem: ListItemRenderer,
                    table: (props) => <pre><TableContainer><Table>{props.children}</Table></TableContainer></pre>,
                    tableHead: (props) => <TableHead>{props.children}</TableHead>,
                    tableBody: (props) => <TableBody>{props.children}</TableBody>,
                    tableRow: (props) => <StyledTableRow>{props.children}</StyledTableRow>,
                    tableCell: (props) => <StyledTableCell>{props.children}</StyledTableCell>,
                    root: (props) => <React.Fragment>{props.children}</React.Fragment>,
                    definition: null,
                    inlineCode: InlineCodeRenderer,
                    code: CodeBlock
                }}
            />
            <ScrollToTop>
                <Fab color="primary" size="small" aria-label="scroll back to top">
                    <KeyboardArrowUpIcon style={{ color: "white" }} />
                </Fab>
            </ScrollToTop>
        </div>
    );
}
